import React, { useState } from "react"
import axios from "axios"

const Contact = () => {
  const [serverState, setServerState] = useState({
    submitting: false,
    status: null,
  })
  const handleServerResponse = (ok, msg, form) => {
    setServerState({
      submitting: false,
      status: { ok, msg },
    })
    if (ok) {
      form.reset()
    }
  }
  const handleOnSubmit = e => {
    e.preventDefault()
    const form = e.target
    setServerState({ submitting: true })
    axios({
      method: "post",
      url: "https://getform.io/f/eacc3445-e893-472b-b925-38ba21fae848",
      data: new FormData(form),
    })
      .then(r => {
        handleServerResponse(true, "Thanks!", form)
      })
      .catch(r => {
        handleServerResponse(false, r.response.data.error, form)
      })
  }
  return (
    <div>
      <div className="secion" id="contact">
        <div className="contact-section">
          <div className="left">
            <h1 style={{ margin: "0px" }}>Hey, let's connect!</h1>
            <p>
              Feel free to reach out. You can send me an email <br />
              or connect on social media.
            </p>
          </div>
          <div className="right">
            <form onSubmit={handleOnSubmit}>
              <div className="form-group">
                <label for="exampleInputName"></label>
                <input
                  type="text"
                  name="name"
                  className="form-control"
                  id="exampleInputName"
                  placeholder="Name"
                  required="required"
                />
              </div>
              <div>
                <label for="exampleInputEmail1" required="required"></label>
                <input
                  type="email"
                  name="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Email"
                />
              </div>
              {/*Message*/}
              <div>
                <label for="exampleInputMessage" required="required"></label>
                <textarea
                  type="text"
                  name="message"
                  className="form-control"
                  id="exampleInputMessage"
                  placeholder="Message"
                />
              </div>
              <button
                type="submit"
                className="submitBtn"
                id="submitBtn"
                disabled={serverState.submitting}
              >
                Send
              </button>
              {serverState.status && (
                <p className={!serverState.status.ok ? "errorMsg" : ""}>
                  {serverState.status.msg}
                </p>
              )}
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Contact
